.checkout__info {
	width: calc(100% - 480px);
	.alert-hide{
		display: none !important;
	}
	.bx-soa-section.bx-selected{
		border: 0;
		border-right: 1px solid #CFD5D5;
	}
	&-step {
		background: #F8FBFB;
		border: 1px solid #CFD5D5;
		border-radius: 4px;
		margin-bottom: 30px;
		position: relative;
		transition: .3s;

		&__num {
			display: flex;
			align-items: center;
			padding: 10px 18px;
			border-bottom: 1px solid #CFD5D5;

			.num {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 31px;
				height: 31px;
				font-weight: 600;
				font-size: 18px;
				margin-right: 17px;
				position: relative;
				border-radius: 50%;
				color: #fff;
				background: #8D979A;

			}

			.text {
				font-weight: 600;
				font-size: 15px;
				line-height: 18px;
				color: #4F5050;
			}
		}

		&__container {
			padding: 30px;
			display: flex;
			flex-wrap: wrap;
			z-index: 100;
			position: relative;
		}

		&__type {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 20px;
			width: 100%;

			.name {
				width: 100%;
				font-weight: 600;
				font-size: 14px;
				color: #232323;
				margin-bottom: 14px;
			}

			label {
				display: flex;
				align-items: center;
				margin-right: 27px;
				cursor: pointer;

				.jq-radio {
					width: 12px;
					height: 12px;
					border: 1px solid #C4C4C4;
					background: none;
					margin-right: 8px;
					box-shadow: none;

					&__div {

					}
				}

				.jq-radio.checked {
					background: #e63728;
					border-color: #e63728;

					.jq-radio__div {
						background: #fff;
						width: 6px;
						height: 6px;
						box-shadow: none;
						margin: 2px 0 0 2px;
					}
				}

				> span {

				}
			}
		}

		&__address {
			width: 100%;
			.name {
				width: 100%;
				font-weight: 600;
				font-size: 14px;
				color: #232323;
				margin-bottom: 10px;
			}

			input[type="text"] {
				width: 100%;
				padding: 13px 0;
				border: 0;
				border-bottom: 1px solid #ACACAC;
				background: none;
				box-shadow: none;
				margin-bottom: 10px;
				color: #000000;

			}

			p {
				font-size: 12px;
				line-height: 15px;
				color: #000000;
				opacity: 0.5;
				margin-bottom: 17px;
			}

			input[type="text"].search--ico {
				padding-left: 40px;
				background-image: url(../images/svg/magnifying-glass.svg);
				background-position: left 10px center;
				background-repeat: no-repeat;
				background-size: 21px;
			}
		}

		&__delPay {
			display: flex;
			flex-wrap: wrap;
			width: 50%;

			&:nth-child(1) {
				border-right: 1px solid #CFD5D5;
				padding-right: 20px;
			}

			&:nth-child(2) {
				padding-left: 20px;
			}

			.block {
				cursor: pointer;
				width: calc(25% - 15px);
				margin-right: 20px;
				position: relative;
				margin-bottom: 15px;
				&:nth-child(4){
					margin-right: 0;
				}

				input[type='checkbox'] {
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					height: 100%;
					opacity: 0;
					cursor: pointer;
					&:hover + .ico{
						background-color: #fff;
					}
				}

				.ico {
					border: 1px solid #CFD5D5;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 4px;
					height: 65px;
					width: 100%;
					margin-bottom: 15px;
					margin-right: 0;
					transition: .2s;

					.sprite-svg {

					}

					img {

					}
				}

				.text {
					text-align: center;

					.name {
						font-size: 12px;
						line-height: 1.2;
						color: #4F5050;
						margin-bottom: 5px;
						display: block;
					}

					.num {
						font-size: 13px;
						line-height: 1.2;
						color: #232323;
						display: block;
						opacity: 0.5;
					}
				}
			}

			.block.active {
				.ico {
					background-color: #fff;
					box-shadow: 0px 0px 10px rgba(213, 221, 221, 0.75);

					svg {
						fill: #1D64A3;
					}
				}
			}
		}

		&__delPay-bot {
			position: absolute;
			display: flex;
			flex-wrap: wrap;
			visibility: hidden;
			opacity: 0;
			top: calc(100% - 10px);
			width: calc(100% + 2px);
			z-index: 1;
			left: -1px;
			padding: 30px;
			background: #EEF4F4;
			border: 1px solid #CFD5D5;
			border-bottom-right-radius: 4px;
			border-bottom-left-radius: 4px;
			transform: rotateX(-180deg);
			transform-origin: 100% 0;
			transition: .5s;

			.block {
				cursor: pointer;
				width: calc(10% + 2px);
				margin-right: 20px;
				position: relative;
				margin-right: 20px;

				&:hover {
					.ico {
						background-color: #fff;
					}
				}

				input[type='checkbox'] {
					position: absolute;
					cursor: pointer;
					left: 0;
					top: 0;
					width: 100%;
					height: 100%;
					opacity: 0;
					&:hover + .ico{
						background-color: #fff;
					}

				}

				.ico {
					border: 1px solid #CFD5D5;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 4px;
					height: 65px;
					width: 100%;
					margin-bottom: 15px;
					margin-right: 0;
					transition: .2s;

					.sprite-svg {

					}

					img {

					}
				}

				.text {
					text-align: center;

					.name {
						font-size: 12px;
						line-height: 1.2;
						color: #4F5050;
						margin-bottom: 5px;
						display: block;
					}

					.num {
						font-size: 13px;
						line-height: 1.2;
						color: #232323;
						display: block;
						opacity: 0.5;
					}
				}
			}

			.block.active {
				.ico {
					background-color: #fff;
					box-shadow: 0px 0px 10px rgba(213, 221, 221, 0.75);

					svg {
						fill: #1D64A3;
					}
				}
			}
		}

		&__delPay-bot.open {
			transform: rotateX(0deg);
			visibility: visible;
			opacity: 1;

		}

		&__promo {
			display: flex;
			padding: 30px;
			justify-content: space-between;
			position: relative;
			align-items: center;
			flex-wrap: wrap;
			.bx-soa-pp-item-container{
				justify-content: center;
			}
			.bx-soa-coupon-item{
				margin-top: 20px;
				display: flex;
				flex-wrap: wrap;
			}
			.bx-soa-coupon-item strong.bx-soa-coupon-item-danger {
				color: #ef4949;
				margin-bottom: 10px;
				padding: 5px 0 5px 10px;
				border: 1px solid #CFD5D5;
			}
			.bx-soa-tooltip.bx-soa-tooltip-danger .tooltip-inner {
				background: #eceeee;
				color: #f58f8f;
			}
			.bx-soa-coupon-item strong:first-child {
				margin-left: 0px;
			}
			.bx-soa-tooltip.bx-soa-tooltip-coupon {
				display: none;
			}
			.bx-soa-coupon-item strong {
				white-space: nowrap;
				margin-right: 20px;
				position: relative;
				text-transform: uppercase;
				font-size: 13px;
				font-weight: bold;
				display: inline-block;
				letter-spacing: -0.5px;
			}
			.bx-soa-coupon-remove {
				display: inline-block;
				height: 20px;
				width: 20px;
				margin-left: 10px;
				background-image: url(../images/svg/close.svg);
				background-size: 10px;
				background-position: center;
				background-repeat: no-repeat;
				cursor: pointer;
				vertical-align: middle;
				position: relative;
				top: -1px;
			}
			.text {
				font-weight: 600;
				font-size: 15px;
				line-height: 1.2;
				color: #4F5050;
			}

			input[type='text'] {
				width: calc(100% - 180px);
				padding: 7px;
				font-size: 15px;
				line-height: 120%;
				color: #000000;
				border: 0;
				border-bottom: 1px solid #ACACAC;
				background: none;
			}

			input[type='submit'] {
				position: absolute;
				right: 30px;
				top: 30px;
				width: 30px;
				height: 30px;
				background: none;
				border: 0;
				background-image: url(../images/svg/ar-red.svg);
				background-position: center;
				background-repeat: no-repeat;
				transition: .3s;
				cursor: pointer;
				border-radius: 50%;

				&:hover {
					right: 25px;
					background-color: #fff;
					box-shadow: 0px 0px 5px #CBD3D3;
				}

			}
		}

		&__info {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			width: 100%;

			.input-text-label {
				width: calc(50% - 20px);
			}

			.input-text-label.w100 {
				width: 100%;
				margin-top: 30px;

				input[type='text'] {
					margin-bottom: 10px;
				}
			}

			input[type='text'],
			input[type='password'] {
				width: 100%;
				padding: 10px 15px;
				border: 0;
				border-bottom: 1px solid #ACACAC;
				max-width: 100%;
				background: none;
				transition: .3s;
				margin-right: 0px;
				margin-bottom: 30px;

				&:focus {
					border-color: #767676;
				}

			}
		}
	}

	&-step.open {
		.checkout__info-step__num .num {
			background-color: #fff;
			color: #4F5050;
			box-shadow: 0px 0px 5px #CBD3D3;

			&::before {
				content: '';
				width: 14px;
				height: 14px;
				background-color: #F74B4B;
				background-image: url(../images/svg/check-w.svg);
				background-position: center;
				background-repeat: no-repeat;
				position: absolute;
				bottom: 0;
				right: -7px;
				border-radius: 50%;
			}
		}
	}

	&-step.open-bot-block {
		margin-bottom: 180px;
	}

	&-submit{
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin-bottom: 30px;
		.jq-checkbox.checked .jq-checkbox__div{
			margin: 4px 0 0 7px;
		}
		label{
			display: block;
			width: 100%;
			text-align: center;
			margin-bottom: 20px;

			a{
				font-size: 15px;
				line-height: 18px;
				margin-left: 15px;
				span{
					color: #000000;
					margin-right: 5px;
				}
			}
		}
	}
}